.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body{
    padding:0;
    margin: 0 auto;
}
.night_btn{
    margin-right:15px;
    margin-left: auto;
    margin-top: auto;
    margin-bottom: auto;
}
.navbar {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    width: 100%;
    display: grid;
    grid-gap: 10px;
    background: rgb(42, 44, 39);
    grid-template-columns: 0fr 1fr;
}
.navbar a{
    text-decoration: none;
}
.navbar .buttons a {
    color: white;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    align-items: center;
}
.navbar .buttons a:hover, .red{
    background: red;
}
.navbar h3{
    margin-left: 15px;
    white-space: nowrap;
    color: rgb(200, 238, 127);
}
.navbar .buttons{
    display: inline-flex;
}
.homeMenu{
    min-height: 100vw;
}
.darkmode .homeMenu{
    background: rgb(36, 36, 36);
}
.darkmode .playersOnline{
    background: rgb(46, 46, 46);
}
.darkmode .playersOnline h3{
    color: rgb(157, 192, 75);
}
.darkmode .playersOnline ul{
    color: rgb(194, 194, 194);
}
.playersOnline{
    display: grid;
    grid-template-rows: 0fr 1fr;
    width: 300px;
    border:1px solid rgb(56, 56, 56);
    border-radius: 12px;
    padding: 10px;
    height: 320px;
}
.playersOnline h3{
    color: black;
    margin:0 auto;
    text-align: center;
}
.playersOnline ul{
    overflow-y: scroll;
    list-style: none;
    padding:0;
    padding-left: 15px;;
}
.playersOnline ul li:not(:last-child) { 
    margin-bottom: 8px;  
 }
.playersOnline ul li::before {
    content: "\2022";  /* Add content: \2022 is the CSS Code/unicode for a bullet */
    color: greenyellow; /* Change the color */
    font-weight: bold; /* If you want it to be bold */
    display: inline-block; /* Needed to add space between the bullet and the text */
    width: 1em; /* Also needed for space (tweak if needed) */
    margin-left: -1em; /* Also needed for space (tweak if needed) */
  }

  /* Hide scrollbar for Chrome, Safari and Opera */
.playersOnline ul::-webkit-scrollbar {
    display: none;
  }
  
  /* Hide scrollbar for IE, Edge and Firefox */
.playersOnline ul{
-ms-overflow-style: none;  /* IE and Edge */
scrollbar-width: none;  /* Firefox */
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
